import '../Detail.css';
import React, { useState} from 'react';
import { Link, useParams } from 'react-router-dom';
import data from '../db/langue';
import IncDecCounter from "../components/IncDecCounter";
import FillExample from '../components/FillExample';
import Button from 'react-bootstrap/Button';


//
import { useDispatch } from 'react-redux'
import { addItem } from './store';

function Detail(props) {
    let { id } = useParams();
let [langue] = useState(data);

// 
let selproduct = props.langue.find((x) => x.id == id); 
let dispatch = useDispatch();

    return (
        <div className='sub_main'>
          <div className="sub_left">
        <div className='smallimgbox'>
        <img src={selproduct.product_img} alt='' />
        </div>
    </div>

    <div className='show_goods'>
    {selproduct.SubImgDetail_set.map((detail, index) => (
            <img key={index} src={detail.imgDetail} alt={`Detail ${index + 1}`} />
        ))}
    </div>

    <div className='box_ti'>
            <h1>{selproduct.title}</h1>
            <p>{selproduct.price}</p>
        </div>

        <div className='copbox'>
           <div className='coptext'>
              <span className='tw-text'> </span>
              <span className='cout-text'>0</span>
            </div>  

            <div className='coupimg'>
                <span className='co-text'></span>
            </div>    
        </div>

        <div className='content'>
             <span className='content-text' >{selproduct.content}</span>
        </div>

    <div className='sub_right2'>
        <h1 className='bey'></h1>
        <span className='point'>540  </span>
        <h1 className='car'> </h1>
        <span className='car2'></span>
        <h1 className='car3'></h1>
        <span className='car4'>3,500(30,000 )   </span>
        <h1 className='car5'></h1>
        <span className='car6'> 3,500</span>
    </div>

    <div className='opt_block'>
         <div className='coun'>
               <span className='coun_text'></span>
         </div>

         <div className='count'>
         <IncDecCounter />

         <span className='area_price'>
            0</span>
         </div>
    
    </div>

    <div className='no-ma'>
        <span className='total-amount'> (0)</span>
        <span className='total_con'>0</span>
    </div>

    <div className='buy_btns'>
       <a>
       <Button className='cash' 
       variant="dark"
       onClick={() => {
        

      dispatch(
          addItem({
          id: selproduct.id,
          name: selproduct.title,
          imgurl: selproduct.product_img.replace("img/", ""),
          count: 1,
          })
      );
      }}
       ></Button>
       </a>
       
       <Link to="/Cart" >
       <Button variant="light"
       className='car_lig'
       ></Button>{' '}
       </Link>

    </div>
    
       <div className='naver'>
      <img src='https://ehdgml123.github.io/rone/img/naver.png' style={{width:"650px", height:"100px"}} />
      
      <div className='nav_wlq'>
      <span className='Wla'></span>
      </div>
       </div>

       <FillExample />
    

        </div>
    );
}

export default Detail;
