
import { Col, Container, Row, Nav } from "react-bootstrap";
import { Link, useNavigate } from 'react-router-dom';
import { Modal, Button } from 'react-bootstrap';
import React, { useState } from 'react';

import '../header.css'


const Header = () => {
  let navigate = useNavigate();
  
  //      
  const [showModal, setShowModal] = useState(false);
  //   
  const handleShowModal = () => setShowModal(true);
  
  //   
  const handleCloseModal = () => setShowModal(false);
 
  //   
  const [isChecked, setIsChecked] = useState(false); 

  const  handleCheckboxChange = (event) => {
    setIsChecked(event.target.checked);
  }

  return (
    <>
      <Container fluid>
        <Row className="nav_wrap">
          <Col className="insticon">
            <img src="https://ehdgml123.github.io/rone/img/pngegg.png" alt="Icon" />
            <div className="line"></div>
          </Col>

          <Col className="search">
            <input className="search_btn" placeholder="Search"></input>
          </Col>

          <Col className="text_table">
            <span>  20%  ,     ,  </span>
          </Col>
          
          <Col>
            <Nav className="nav_title">
              <Nav.Link href="#" onClick={handleShowModal}></Nav.Link>
              <Nav.Link href="#"></Nav.Link>
              <Nav.Link onClick={() => navigate("/Cart")}></Nav.Link>
            </Nav>
            <Modal show={showModal} onHide={handleCloseModal}>
        <Modal.Header closeButton>
          <Modal.Title></Modal.Title>
        </Modal.Header>
        <Modal.Body>
    
          <input type="text" className="id" placeholder=""  />
          <input type="text" className="password" placeholder=""  />
          <div>
      <label className="checkbox">
        <input
          type="checkbox"
          className="box_check"
          style={{width:'50px'}}
          checked={isChecked}
          onChange={handleCheckboxChange}
        />
          
      </label>
      <p > : {isChecked ? '' : ' '}</p>
    </div>
          <Button variant="dark" className="btn_login"></Button>
          
          <div className="over_h">
            <a href="#" className="float_l"></a>
            <a href="#" className="float_r">   </a>
          </div>
          <Button variant="secondary" className="qlgodnjs_btn">  </Button>{' '}
        </Modal.Body>
        <Modal.Footer>
       
        </Modal.Footer>
      </Modal>
          </Col>
        </Row>
      </Container>

      <Row className="logo">
        <Link to="/" className="logolink">
          <img src="https://ehdgml123.github.io/rone/img/logo.png" alt="Logo" />
        </Link>
      </Row>
    </>
  );
}

export default Header;
